<template>
  <div class="pattern-cubes">

    <h1>Pattern Cubes</h1>

    <input
      type="radio"
      v-model="stroke"
      v-bind:value=true
    >
    <input
      type="radio"
      v-model="stroke"
      v-bind:value=false
    >

    <select v-model="selected">
      <option
        v-for="(option, k) in cubesPattern"
        v-bind:value="option"
        v-bind:key="k"
      >{{k}}
      </option>
    </select>

    <svg
      id="dynamicIllustration"
      :viewBox="'0 0 '+pace*4+' '+pace*4"
      :width="pace*4+'px'"
      :height="pace*4+'px'"
      style="border: 0.2 solid #000"
      :transform="transform ? 'rotate(45) translate(0,120)' : ''"
    >
      <g
        v-for="(h, i) in selected"
        :key="i"
        v-html="path(h[0], h[1], h[2], colors[h[3]])"
      />
      <g
        v-if="stroke"
        v-html="grid()"
      />
      <!-- <g v-html="path(0, 1, true, this.by)"> -->
    </svg>
    <a
      href="#"
      @click="downloadSVG($event, 'house')"
    >Download</a>
  </div>
</template>

<script>
import cubesPattern from "@/assets/cubesPattern.js";
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    return {
      cubesPattern: cubesPattern,
      selected: cubesPattern.base,
      stroke: false,
      colors: {
        yy: ['#ffef3a', '#ffef3a'],
        bb: ['#1db0ec', '#1db0ec'],
        yb: ['#ffef3a', '#1db0ec'],
        by: ['#1db0ec', '#ffef3a'],
        rr: ['#ea353d', '#ea353d'],
        ww: ['#ffffff', '#ffffff'],
        rw: ['#ea353d', '#eeeeee'],
        wr: ['#eeeeee', '#ea353d']
      },
      pace: 50,
      transform: false
    }
  },
  methods: {
    path: function (x, y, rotation, colors) {
      let h = this.pace
      x = x * this.pace
      y = y * this.pace
      if (rotation) {
        x += this.pace
        h = -this.pace
      }
      let path = `
        <path d="M` + x + `,` + y + ` h` + h + ` v` + this.pace + ` z" fill="` + colors[0] + `"/>
	<path d="M` + x + `,` + y + ` v` + this.pace + ` h` + h + ` z" fill="` + colors[1] + `"/>`
      return path
    },
    grid: function () {
      let grid = ''
      let maxX = 0
      let maxY = 0
      for (let i in this.selected) {
        if (this.selected[i][0] > maxX) maxX = this.selected[i][0]
        if (this.selected[i][1] > maxY) maxY = this.selected[i][1]
      }
      maxX++
      maxY++
      for (let i = 1; i < maxX; i++) {
        grid += '<line x1="' + (i * this.pace) + '" y1="0" x2="' + (i * this.pace) + '" y2="' + (maxY * this.pace) + '" style="stroke: black; stroke-width: 1" />'
      }
      for (let i = 1; i < maxY; i++) {
        grid += '<line x1="0" y1="' + (i * this.pace) + '" x2="' + (maxX * this.pace) + '" y2="' + (i * this.pace) + '" style="stroke: black; stroke-width: 1" />'
      }
      return grid
    },
    downloadSVG (evt, name) {
      const svgContent = document.getElementById("dynamicIllustration").outerHTML,
        blob = new Blob([svgContent], {
          type: "image/svg+xml"
        }),
        url = window.URL.createObjectURL(blob),
        link = evt.target;
      link.target = "_blank";
      link.download = name + ".svg";
      link.href = url;
    }
  }
}
</script>

